@mixin onPhone {
  @media (max-width: 599px) {
    @content;
  }
}

@mixin onTablet {
  @media (min-width: 600px) and (max-width: 1023px) {
    @content;
  }
}

@mixin onDesktop {
  @media (min-width: 1024px) {
    @content;
  }
}

@mixin onTablet-Desktop {
  @media (min-width: 600px) {
    @content;
  }
}
